/* Cart page */
.page-cart {
    margin-top: 30px;
    @media (min-width: 750px) {
        margin-top: 60px;
    } 
    .section-cart-template {
        margin-bottom: 40px; 
        @media (min-width: 750px) {
            margin-bottom: 60px; 
        }  
    }
    .cart-empty {
        display: none;
        p { 
            font-size: 24px; 
        } 
        .btn {
            max-width: 412px;
            margin: 0 auto;  
        }
    }
    .cart-wrapper {
        &[data-status="0"] {
            .cart-form {
                display: none;
            }
            .cart-empty {
                display: block;
            }
        }
    }
    .cart-table {
        background-color: var(--second-bg-color);
        margin-bottom: var(--gutter-xlg); 
        @media (min-width: 750px) {
            margin-bottom: var(--gutter-sm); 
        }
        .cart-table_head {
            display: none;
            @media (min-width: 750px) {
                display: flex; 
                border-bottom: 1px solid var(--body-border-color);
            }
            >div {
                font-size: calc(var(--body-font-size) + 2px);
                font-weight: var(--font-bold);
                flex: 0 0 16.66667%;
                max-width: 16.66667%; 
                text-align: center;  
                padding: 16px var(--gutter-xs);
                &.cart-table_head-product {
                    flex: 0 0 39.66666%;
                    max-width: 39.66666%;
                }
                &.cart-table_head-quantity {
                    flex: 0 0 18.66666%;
                    max-width: 18.66666%;
                }
                &.cart-table_head-remove {
                    flex: 0 0 8.33333%;
                    max-width: 8.33333%; 
                }
            }
        }
        .cart-table_body {
            .cart-table_line-item {
                display: flex;
                align-items: center;  
                flex-wrap: wrap;
                position: relative; 
                animation-duration: 0.5s;
                animation-fill-mode: both;
                &:not(:first-child) {
                    border-top: 1px solid var(--body-border-color); 
                }
                >div {
                    flex: auto;  
                    max-width: none; 
                    padding: 20px var(--gutter-xs); 
                    text-align: center;
                    @media (min-width: 750px) {
                        padding-top: 24px; 
                        padding-bottom: 24px; 
                        flex: 0 0 16.666667%;
                        max-width: 16.666667%;
                    }
                    &.line-item_info {
                        flex: 0 0 95%;
                        max-width: 95%;
                        text-align: start;
                        display: flex;
                        align-items: center; 
                        padding-bottom: 12px;
                        @media (min-width: 750px) {
                            flex: 0 0 39.66666%;
                            max-width: 39.66666%;
                            padding-bottom: 24px; 
                            @include padding-start(30px);  
                        } 
                    } 
                    &.line-item_price {
                        text-align: start; 
                        padding-top: 0;
                        @include padding-start(120px); 
                        @media (min-width: 576px) {
                            @include padding-start(136px); 
                        }
                        @media (min-width: 750px) {
                            padding-top: 24px;
                            text-align: center;
                            @include padding-start(15px); 
                        }
                    }
                    &.line-item_quantity { 
                        padding-top: 0; 
                        @media (min-width: 750px) {
                            padding-top: 24px;
                            flex: 0 0 18.66666%;
                            max-width: 18.66666%;
                        }
                    } 
                    &.line-item_total-price {
                        display: none; 
                        @media (min-width: 750px) { 
                            display: block;
                            flex: 0 0 16.666667%;
                            max-width: 16.666667%; 
                        }
                    }
                    &.line-item_remove {
                        flex: auto;
                        max-width: none;
                        @include ab(30px, n, n, 0);
                        @media (min-width: 750px) {
                            position: static;
                            flex: 0 0 8.333333%;
                            max-width: 8.333333%;
                        }
                    }
                }
                &.is-removing {
                    animation-name: remove-items;
                } 
            }
            .line-item_image {
                flex: 0 0 90px; 
                @include margin-end(15px);
                @media (min-width: 576px) {
                    flex: 0 0 116px; 
                }
                @media (min-width: 750px) {
                    flex: 0 0 90px;
                    @include margin-end(20px); 

                } 
            } 
            .line-item_wrapper {
                flex: 1;
                @include margin-end(10px);
                @media (min-width: 750px) {
                    @include margin-end(0);
                }
            }
            .line-item_title {
                margin-bottom: 7px;
                font-size: calc(var(--body-font-size) + 2px); 
            }
            .line-item_sku {
                color: var(--text-miscellaneous);  
            }  
            .line-item_price, .line-item_total-price {
                font-size: calc(var(--body-font-size) + 2px); 
                color: var(--price-color);
                font-weight: var(--font-bold);
                @media (min-width: 750px) {
                    font-weight: var(--font-body-weight); 
                }
            } 
            .price-sale {
                color: var(--price-sale-color);
                display: block; 
            } 
            .price-compare {
                display: block;
                color: var(--price-compare-color);
                font-weight: var(--font-body-weight);
                font-size: var(--body-font-size);  
            } 
            .input-group {
                max-width: 110px;
                border: 1px solid var(--body-border-color);
                border-radius: var(--input-radius);
                margin: 0 auto; 
                quantity-input {
                    display: flex;
                    height: 100%;
                    quantity-input-minus, quantity-input-plus {
                        display: flex;
                        align-items: center;
                        padding: 0 10px;
                    }
                    input {
                        border: none;
                        background: transparent;
                        width: 100%;
                        height: 48px;
                        text-align: center; 
                        padding: 0;
                    }
                }
            }  
            .btn-remove {
                width: 24px;
                height: 24px;
                display: flex;
                margin: 0 auto;
                align-items: center; 
                justify-content: center; 
                border-radius: 50%;
                background: #000000; 
                .svg-close {
                    width: 10px;
                    height: 10px;
                    path {
                        fill: #ffffff;
                    }
                }
                .svg-loading {
                    display: none;  
                    width: 14px;
                    height: 14px;
                    circle {
                        stroke: #ffffff;
                    }
                }
                &.pending {
                    .svg-close {
                        display: none;
                    }
                    .svg-loading {
                        display: block;
                    }
                }
            }
        }
    }
    .btn-continue {
        color: #086479;
        text-decoration: underline;
        margin-bottom: 29px; 
        display: none;
        @media (min-width: 750px) {
            display: inline-block;
        }
        &:hover {
            @media (hover: hover) {
                color: var(--body-link-hover); 
            }
        } 
    } 
    .note-and-shipping {
        background-color: var(--second-bg-color);
        margin-bottom: 40px;
        padding: var(--gutter-lg) var(--gutter-xs); 
        @media (min-width: 750px) {
            padding: 24px; 
        }
        .cart-note {
            margin-top: 31px;
        }
        .cart-note_title {
            margin-bottom: 5px; 
            >span:first-child {
                font-size: calc(var(--body-font-size) + 2px);
            }
        }  
    }
    .cart-shipping-calculator {
        .shipping-calculator_form {
            align-items: flex-end; 
            flex-wrap: wrap; 
        } 
        div.shipping-calculator_title { 
            font-size: calc(var(--body-font-size) + 2px);
            font-weight: var(--font-bold); 
            margin-bottom: 16px; 
        }  
        .form-group {
            flex: 0 0 100%;
            margin-bottom: 17px;
            @media (min-width: 750px) {
                margin-bottom: 7px;
                flex: 1;
                max-width: 224px;
                width: 100%;
                @include padding-end(var(--gutter-xxs));
            }
            .select-group {
                width: 100%;
                position: relative;
                select {
                    width: 100%;
                }
                svg {
                    @include ab(50%, n, n, 10px);
                    transform: translate(0, -50%); 
                    pointer-events: none;
                }
            }
            input {
                width: 100%; 
            }
        }
        .btn {
            padding: 8px var(--gutter-xxs);
            margin-bottom: 7px;
            width: 100%; 
            position: relative;
            @media (min-width: 750px) {
                max-width: 190px;
            }  
            .svg-loading {
                display: none;
                @include ab(50%, 50%, n, n);
                transform: translate(-50%, -50%);
            } 
        }
        .pending .btn {
            span {
                color: transparent;
            }
            .svg-loading {
                display: block;
            }
        }
        .shipping-calculator_errors {
            display: flex;
            align-items: center;
            color: #ed462f;
            margin-bottom: var(--gutter-xs);
            svg {
                flex: 0 0 10px;
                @include margin-end(5px);
            }
        }
        .shipping-calculator_results {
            p {
                &:empty {
                    margin: 0;
                }
                &:not(:empty) {
                    margin: 0 0 5px;
                }
            }
            ul:not(:empty) li {
                @include list-style-reset;
                &:not(:last-child) {
                    margin-bottom: 5px; 
                }
            } 
        }
    }
    .cart-summary {
        .cart-summary_wrapper { 
            background: var(--second-bg-color); 
            padding: var(--gutter-sm) var(--gutter-xs) var(--gutter-md);    
            @media (min-width: 750px) {
                padding: 24px; 
            }
        }
        .cart-summary_header {
            margin-bottom: var(--gutter-xs); 
        }
        .cart-summary_body {
            border-bottom: 1px solid rgba(212, 212, 212, 0.3);  
            >div {
                margin-bottom: var(--gutter-sm);
            }
        }
        .cart-summary_label {
            font-size: calc(var(--body-font-size) + 2px);
            color: var(--body-color-2); 
        }
        .cart-summary_price {
            font-size: calc(var(--body-font-size) + 2px);
            font-weight: var(--font-bold); 
        }
        .cart-summary_label {
            margin: 0; 
        }
        .cart-summary_footer {
            padding-top: 19px;  
            .cart-summary_total {
                margin-bottom: 21px; 
                text-transform: uppercase;  
            }
            button {
                width: 100%; 
                position: relative;
                .svg-loading {
                    display: none;
                    @include ab(50%, 50%, n, n);
                    transform: translate(-50%, -50%);
                }
                &.pending {
                    span {
                        color: transparent;
                    }
                    .svg-loading {
                        display: block;
                    }
                }
            }
        }
        .other-payment {
            margin-top: var(--gutter-xs);
            color: var(--body-color-2);
            .payment-gateways {
                margin-top: 6px;
            }
            .payment-list {
                justify-content: center;
                li {
                    display: flex;
                    margin: 5px; 
                } 
            }
        }
        .cart-summary_coupon {
            align-items: center; 
        } 
    } 
}
/* End Cart page */ 